/**
 * Returns information about the platform this function is being run on.
 * @internal
 */
export function getPlatformInfo() {
  return "(javascript-Browser-".concat(getReleaseInfo(), ")");
}
/**
 * Returns information about Node.js this function is being run on.
 * @internal
 */

export function getFrameworkInfo() {
  return "Browser/".concat(getReleaseInfo());
}
/**
 * @internal
 *
 * @returns
 */

function getReleaseInfo() {
  if (typeof self === "undefined") {
    return "";
  }

  var navigator = self.navigator;
  return navigator.appVersion;
}